import React, { useState } from 'react'
import { Container, Form, Nav, Navbar, NavDropdown, Row } from 'react-bootstrap'

 const NavBar = ({filterbySearch}) => {
  const[searchValue,setsearchvalue]=useState('');
  const onSearch=(e)=>{
    e.preventDefault();
    filterbySearch(searchValue);
    setsearchvalue('');
  }
  return (
    <Row>
    <Navbar bg="dark" expand="lg" variant='dark'>
      <Container >
       
        <Navbar.Brand href="/">
        
        <div className='brand-color'>
            
                 </div> 
        </Navbar.Brand>
      
        <NavDropdown className='brand-color mx-2' title="  " id="basic-nav-dropdown">
              <NavDropdown.Item href="/ProductsPageLocalStores"> </NavDropdown.Item>
              <NavDropdown.Item href="/ProductsEcommercePage">
                 
              </NavDropdown.Item>
              <NavDropdown.Item href="/ProductsFamilesProductsPage"> </NavDropdown.Item>
              <NavDropdown.Divider />
            
        </NavDropdown>
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">
          <Nav
            className="me-auto my-2 my-lg-0"
            style={{ maxHeight: '100px' }}
            navbarScroll
          >
       
          </Nav>
          <Form className="d-flex">
            <Form.Control
              type="text"
              placeholder=""
              className="mx-2"
              onChange={(e)=>setsearchvalue(e.target.value)}
              value={searchValue}
            />
            <button onClick={onSearch} className="btn-search"></button>
          </Form>
        </Navbar.Collapse>
      </Container>
    </Navbar>



    </Row>
  )
}
export default NavBar