import React from 'react'
import { Col, Row } from 'react-bootstrap'


 const StoreText = ({store}) => {
 
    console.log(store)
  return (
    <div>
    <Row className="mt-4">
      <div className="cat-text">  :</div>
    </Row>
    <Row>
      <Col md="8">
        <div className="cat-title d-inline">
         :
          <div className="cat-rate d-inline mx-3">{store.name}</div>
        </div>
        
      </Col>
    </Row>
    <Row>
      <Col md="8">
        <div className="cat-title d-inline">
          :
          <div className="cat-rate d-inline mx-3">{store.typeName}</div>
        </div>
        
      </Col>
    </Row>
    <Row>
      <Col md="8">
        <div className="cat-title d-inline">
         :
          <div className="cat-rate d-inline mx-3">{store.categoryName}</div>
        </div>
        
      </Col>
    </Row>



    <Row className="mt-2">
      <div className="cat-text">:</div>
    </Row>
    <Row>
      <Col md="8">
        <div className="cat-title d-inline">
         :
          <div className="cat-rate d-inline mx-3">{store.regionName}</div>
        </div>
        
      </Col>
    </Row>
    <Row>
      <Col md="8">
        <div className="cat-title d-inline">
         :
          <div className="cat-rate d-inline mx-3">{store.cityName}</div>
        </div>
        
      </Col>
    </Row>
    <Row>
      <Col md="8">
        <div className="cat-title d-inline">
         :
          <div className="cat-rate d-inline mx-3">{store.districtsName}</div>
        </div>
        
      </Col>
    </Row>
    <Row>
      <Col md="8">
        <div className="cat-title d-inline">
         :
          <div className="cat-rate d-inline mx-3">{store.address}</div>
        </div>
        
      </Col>
    </Row>

    <Row>
      <Col md="8">
        <div className="cat-title d-inline">
         :
          <div className="cat-rate d-inline mx-3">{store.address}</div>
        </div>
        
      </Col>
    </Row>

    


    <Row>
      <Col md="8" className="mt-4">
        <div className="cat-text d-inline"> :</div>
        <div className="barnd-text d-inline mx-1">{store.rating} </div>
      </Col>
    </Row>
 

    <Row className="mt-4">
      <div className="cat-text"> :</div>
    </Row>
    <Row className="mt-2">
      <Col md="10">
        <div className="product-description d-inline">
          {store.name}
        </div>
      </Col>
    </Row>
    <Row className="mt-4">
      <Col md="12">

        <div className="product-cart-add px-3 py-3 d-inline mx-1"> </div>
        <div className="product-cart-add2 px-3 py-3 d-inline mx-1">  </div>
      </Col>
    </Row>
  </div>
  )
}
export default StoreText









